.account {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid black;
    background-color: #fff;
    width: 80%;
    margin: 0 auto;
    flex-direction: column;
    padding: 1.5rem;
    box-sizing: border-box;
    text-align: left;
    margin-bottom: 2rem;

    &-amount {
        margin: 0;
        font-size: 2.5rem;
        font-weight: bold;

        &-description {
            margin: 0;
        }
    }

    &-title {
        margin: 0;
        padding: 0;
        font-size: 1rem;
        font-weight: normal;
    }

    &-content-wrapper {
        width: 100%;
        flex: 1;

        & button {
            width: 200px;
        }
    }
}

.user-account {
    padding: 30px 0;
}

@media (min-width: 720px) {
    .account {
        flex-direction: row;
    }

    .account-content-wrapper.cta {
        flex: 0;
    }
}